<template>
  <div class="container">
    <div class="search-dv">
      <van-search placeholder="请输入项目别名或工作简介" v-model="searchText"/>
    </div>
    <div class="main">
      <div class="main-row" style="width: 100%">
        <van-row>
          <van-col span="12" v-for="(item,index) in displayWorkItem" :key="index" :ref="item.id">
            <div class="col-bkg" @click="showWorkItemById(item.intro_id)">
              <div class="project-top">
                <span>{{item.project_name}}</span>
              </div>
              <div class="project-bottom">
                <span>{{item.work_intro}}</span>
              </div>
            </div>
          </van-col>
        </van-row>
      </div>
      <div style="height: 44px"></div>
      <div class="footer">
        <van-tabbar v-model="active">
          <van-tabbar-item to="/WorkDailyManage" icon="wap-home">首页</van-tabbar-item>
          <van-tabbar-item icon="add-o" @click="addWorkItem">新增</van-tabbar-item>
          <van-tabbar-item to="/MyInfo" icon="contact">我的</van-tabbar-item>
        </van-tabbar>
      </div>
    </div>
    <router-view/>
  </div>
</template>

<script>
  export default {
    name: "WorkItemManage",
    beforeCreate() {
      document.querySelector('body').setAttribute('style', 'background:#f3f3f3')
    },
    beforeDestroy() {
      document.querySelector('body').removeAttribute('style');
    },
    data() {
      return {
        searchText: '',
        active: -1,
        showAddWorkItem: false,
        imgUrl: require('../assets/images/work/card-background.png'),
      }
    },
    computed: {
      displayWorkItem() {
        return this.$store.state.displayWorkItem;
      }
    },
    mounted() {
      this.$nextTick(res => {
        this.searchWorkItemHandle();
      });
    },
    methods: {
      /* 查询工作简介--start */
      searchWorkItemHandle() {
        this.$api.get('/iims/intros/data').then( res => {
          res.status = parseInt(res.status);
          if (res.status === 1) {
            var datas = [];
            res.data.some((item,i) => {
              datas.push(item);
            });
            this.$store.commit('storeAllWorkItem', datas); // 将获取的工作简介数据追加到store定义的allWorkItem变量中
            this.$store.commit('storeDisplayWorkItem', datas);
          }
        }).catch(err => {
          console.log(err);
        });
      },
      /* 查询工作简介--end */

      /*进入工作简介详情页面--start*/
      showWorkItemById(id) {
        this.$store.commit('changeJudgeAdd',false);
        this.$router.push({
          name: 'EditWorkItem',
          query: {introId: id} // 获取上一页数据的id，跳转到的下一页可以通过（var id = this.$route.query.introId;）获取
        });
      },
      /*进入工作简介详情页面--end*/

      /*新增工作简介--start*/
      addWorkItem() {
        this.$store.commit('changeJudgeAdd', true);
        this.$router.push({
          name: 'EditWorkItem',
          query: {introId: -1}
        })
      }
      /*新增工作简介--end*/
    },
    watch: {
      // 查询工作简介
      searchText(value) { // newText是新输入的值
        var data = this.$store.getters.doneDisplayWorkItem(value);
        this.$store.commit('storeDisplayWorkItem', data);
      }
    }
  }
</script>

<style lang="less" scoped>
  .container {
    height: 100%;
    background-color: #f3f3f3;

    .van-search {
      background-color: #f3f3f3 !important;
      padding: 20px;
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      z-index: 1;

      .van-search__content {
        background-color: #fff;
        border-radius: 30px;

        .van-cell {
          color: #fe8f01;

          /deep/ .van-field__left-icon .van-icon {
            font-size: 22px;
          }
        }
      }
    }

    .main {
      display: flex;
      flex-flow: column;
      padding: 10px 10px;
      margin-top: 54px;

      .van-row {
        height: 20%;

        .van-col {
          padding: 8px;

          .col-bkg {
            display: flex;
            flex-flow: column;
            height: 200px;
            background: url('../assets/images/work/card-background.png');
            background-size: cover;
          }
        }

        .project-top {
          padding: 0 20px;
          text-align: center;
          margin-top: 32%;

          span {
            color: #fe8f01;
            font-weight: bold;
            display: -webkit-box;
            overflow: hidden;
            white-space: normal !important;
            text-overflow: ellipsis;
            word-wrap: break-word;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical
          }
        }

        .project-bottom {
          margin-top: 16%;
          padding: 0 10px;
          text-align: center;

          span {
            font-size: 0.6rem;
            display: -webkit-box;
            overflow: hidden;
            white-space: normal !important;
            text-overflow: ellipsis;
            word-wrap: break-word;
            -webkit-line-clamp: 3;
            -webkit-box-orient: vertical
          }
        }
      }

      .van-popup{
        width: 200px;
        height: 200px;
      }
    }
  }


</style>
